.current {
    text-decoration: underline;
}
body {
  background-color: #abc5f4;
}
main {
  background-color: white;
  border : 1px solid #90b3f4;
  border-radius: 10px;
}
.jumbotron {
  /* margin-bottom: 0 !important; */
}
#content {
  margin-top : 0;
  max-width: 800px;
}
main h1 {
  font-family: "Times New Roman", Georgia, serif;
}
#content  p {
    max-width : 600px;
    font-family: "Times New Roman", Georgia, serif;
}
@media screen and (min-width: 900px) {
  main {
    padding: 1rem 5rem;
    min-height : 500px;
  }
  main div {
    padding: 4rem 4rem;
  }
}
@media screen and (max-width: 392px) {
    .jumbotron {
        font-size: 2.5rem;
    }
}